import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-java.webp';
import reference from '../../../img/java/reference.png';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';
import 'prismjs/components/prism-java';

const Types = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className='cover-wrapper'>
                <img src={cover} alt='java cover' className='cover' />
            </div>
            <article className='article article-content'>
                <h1>Referencja</h1>
                <p>Jest to zmienna obiektowa, ktra wskazuje (odwouje si) do obiektu.</p>

                <div className='article-img-wrapper'>
                    <img src={reference} alt='reference' className='article-img' />
                </div>

                <p className='snippets'>
                    Moemy teraz wywoywa metody klasy <span>String</span> dla tego obiektu
                    <span>(String "rower")</span>, zarwno za porednictwem zmiennej <span>a</span> jak i <span>b</span>
                    - i bdzie to miao dokadnie ten sam skutek. Koniec kocw metody te bd wywoywane na tym samym
                    obiekcie klasy <span>String</span>.
                </p>

                <p className='snippets'>
                    Zmienna obiektowa nie jest obiektem, tylko referencj do obiektu. Warto kadej zmiennej obiektowej
                    jest referencj do obiektu, ktry jest przechowywany gdzie indziej. Warto zwracana przez
                    operator <span>new</span> te jest referencj. Instrukcja typu:
                </p>

                <pre className='line-numbers language-java'>
                    <code>
{`Date deadline = new Date();`}
                    </code>
                </pre>

                <p className='snippets'>
                    skada si z dwch czci. Wyraenie <span>new Date();</span> tworzy obiekt typu <span>Date</span>,
                    a jego wartoci jest referencja do tego nowo utworzonego obiektu.
                </p>

                <p className='snippets'>
                    Referencja zostaje zapisana w zmiennej <span>deadline</span>. Aby zaznaczy, e zmienna obiektowa
                    nie odwouje si do adnego obiektu, trzeba jej warto ustawi na <span>null</span>, bo zmienne
                    obiektowe nie s automatycznie inicjalizowane wartoci <span>null</span>.
                </p>

                <hr />

                <h1>String Pool</h1>
                <p className='snippets'>
                    W Javie wszystkie obiekty przechowywane s w pamici w stercie pamici
                    (<strong>Memory Heap</strong>). To na niej dziaa Garbage Collector i jeli dostaniemy kiedy
                    <span>OutOfMemoryException</span> to znaczy, e zbytnio zaszalelimy z tworzeniem nowych bytw i
                    zapenilimy ca stert szybciej ni zwalnialimy miejsce. W tej stercie znajduje si wydzielony
                    obszar pamici przeznaczony do przechowywania Stringw, ktrych to w programie potrafi by
                    <u>bardzo, bardzo duo</u> - dlatego te s obsugiwane w specjalny sposb. Wanie do tego
                    suy <strong>String Pool</strong>.
                </p>

                <p className='snippets'>
                    Za kadym razem, gdy stworzymy nowego Stringa w ten sposb <span>String name = "John"</span>, to w
                    String jest tworzony w <strong>String Pool</strong> i tam jest przechowywany. Jaki czas pniej,
                    przy tworzeniu innego Stringa <span>String anotherName = "John"</span> nie tworzy si kolejny wyraz
                    "John" w pamici, lecz oba wskazuj na ten sam element w <strong>String Pool</strong>. Dlatego te w
                    tym przypadku porwnanie <span>name == anotherName</span> zwrci nam <span>true</span>, bo w tym
                    wypadku obie referencje wskazuj na ten sam obiekt.
                </p>

                <p className='snippets'>
                    Sprawy maj si inaczej, gdy stworzymy Stringa w ten sposb
                    <span>String yetAnotherName = new String("John")</span>. Wwczas jest tworzony za kadym razem nowy
                    obiekt w <strong>Memory Heap</strong>, a dopiero on wskazuje na obiekt w <strong>String
                    Pool</strong>. Dlatego porwnanie <span>yatAnotherName == name</span> zwrci <span>false</span>, bo
                    fizycznie s to referencje do dwch rnych obiektw. Dlatego te po pierwsze nie powinno si robi
                    nigdy <span>new String("...")</span>, bo tworzy to dwa obiekty (w <strong>Memory Heap</strong> i
                    opcjonalnie w <strong>String Pool</strong>, zamiast tylko w <strong>String Pool</strong>). Po drugie
                    do porwnywania Stringw uywamy metody <span>.equals</span> zamiast <span>==</span>, gdyby wanie
                    kto przypadkiem stworzy tekst za pomoc <span>new String</span>. Metoda <span>.equals</span>
                    porwnuje faktyczne wartoci (czyli w przypadku Stringw to co jest w <strong>String Pool</strong>),
                    a nie na co wskazuje referencja.
                </p>

                <hr />

                <h1>Immutable</h1>
                <p>
                    W programowaniu istnieje wana koncepcja zwana <strong>Immutable</strong> i oznacza, e obiekt
                    zawsze przechowuje te same wartoci. Jeli musimy zmodyfikowa te wartoci, powinnimy utworzy nowy
                    obiekt. Typowym przykadem jest klasa <strong>String</strong>. <strong>Stringi</strong> s
                    niezmiennymi obiektami, wic wszystkie operacje na nich generuj nowy cig. Niezmienne typy
                    umoliwiaj pisanie programw z mniejsz liczb bdw.
                </p>

                <p className='snippets'>
                    Klasa <span>Patient</span> nie jest niezmienna, poniewa istnieje moliwo zmiany dowolnego pola
                    obiektu.</p>

                <pre className='line-numbers language-java'>
                    <code>
{`Patient patient = new Patient();

patient.name = "Mary";
patient.name = "Alice";`}
                    </code>
                </pre>
            </article>
        </>
    );
};

export default Types;
