import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-javascript.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const TruthyFalsy = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className='cover-wrapper'>
                <img src={cover} alt='html cover' className='cover' />
            </div>
            <article className='article article-content'>
                <h1>truthy / falsy</h1>
                <p>
                    Tworzc warunki, nie musimy porwnywa ze sob dwch wartoci. Wartoci false staje si kada z
                    poniszych wartoci. S to tak zwane wartoci falsy:
                </p>
                <pre className='line-numbers language-js'>
                    <code>
{`if (false) {...}
if (null) {...}
if (undefined) {...}
if (0) {...}
if (NaN) {...}
if ("") {...}
if (document.all) {...}`}
                    </code>
                </pre>

                <p>
                    Dla nas oznacza to tyle, e tworzc warunki moemy wykonywa kod w zalenoci od stanu danej
                    zmiennej, ale te wartoci wpisanej rcznie w nawias:
                </p>
                <pre className='line-numbers language-js'>
                    <code>
{`const a = 20;
const b = 0;
const c = null;

if (a) { //to si wykona bo a !== 0
    console.log("A ma warto ", a);
}

if (b) { //to si nie wykona bo b === 0
    console.log("A ma warto ", b);
}

if (c) { //to si nie wykona bo null
    console.log("A ma warto ", c);
}

if (false) { //to si nie wykona bo false to false
    console.log("false ma warto ", false);
}`}
                    </code>
                </pre>

                <p className='snippets'>
                    Kada inna warto daje w rezultacie true. Moemy to sprawdzi konwertujc dane wartoci
                    na <span>Boolean</span>:
                </p>
                <pre className='line-numbers language-js'>
                    <code>
{`Boolean(false); //false
Boolean(null); //false
Boolean(undefined); //false
Boolean(0); //false
Boolean(NaN); //false
Boolean(""); //false
Boolean(document.all); //false

Boolean("Ala"); //true
Boolean(2); //true
Boolean(2-2); //false

const x;
Boolean(x); //false bo x nie ma wartoci czyli undefined`}
                    </code>
                </pre>

                <p>Powysze sprawia, e do czsto spotkasz zapisy jak poniej:</p>
                <pre className='line-numbers language-js'>
                    <code>
{`if (nr) { //kod si wykona jeeli warto liczby nr jest rna od falsy
    ...
}

const txt = "Ala ma kota";
if (txt.length) { //sprawdzam dugo tekstu. Jeeli wiksza od 0 to true
    ...
}

const tab = []
if (tab.length) { //podobnie sprawdzam dugo tablicy
    ...
}`}
                    </code>
                </pre>
            </article>
        </>
    );
};

export default TruthyFalsy;
