// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';
import 'prismjs/components/prism-java'

const Types = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className='cover-wrapper'>
                <img src={cover} alt='java cover' className='cover' />
            </div>
            <article className='article article-content'>
                <h1>Klasa abstrakcyjna a Interfejs - rnice</h1>

                <table className='table'>
                    <tbody>
                        <tr>
                            <td />
                            <td><strong>Klasa abstrakcyjna</strong></td>
                            <td><strong>Interfejs</strong></td>
                        </tr>
                        <tr>
                            <td>pola</td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>pola - wyjtek</td>
                            <td><span>final</span></td>
                            <td><span>public static final</span></td>
                        </tr>
                        <tr>
                            <td>konstruktor</td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>dziedziczenie po</td>
                            <td>1(<span>extends</span>)</td>
                            <td>(<span>implements</span>)</td>
                        </tr>
                        <tr>
                            <td>instancja</td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>metody</td>
                            <td><span>abstract</span> + inne</td>
                            <td><span>public abstract</span></td>
                        </tr>
                        <tr>
                            <td>metoda abstrakcyjna - ciao</td>
                            <td>brak</td>
                            <td>brak</td>
                        </tr>
                        <tr>
                            <td>metoda abstrakcyjna - ciao - wyjtek</td>
                            <td>brak</td>
                            <td><span>default</span></td>
                        </tr>
                    </tbody>
                </table>

                <hr />

                <h1>abstract class</h1>
                <ul className='snippet-list'>
                    <li>tworz warstw abstrakcji, ktra jest mao precyzyjna</li>
                    <li>maj upraszcza rzeczywisto</li>
                    <li>nie mona stworzy instancji klasy abstrakcyjnej</li>

                    <details>
                        <summary>Przykad</summary>
                        <p>Mamy w programie tak oto klas abstrakcyjn:</p>
                        <pre className='line-numbers language-java'>
                            <code>
{`abstract class MySwingWorker<T, V> extends SwingWorker<T, V> { 
    public final void myPublish(V... args) {
        publish(args);
    }
}`}
                            </code>
                        </pre>

                        <p>Wewntrz klasy uruchomieniowej nie uda si stworzy instancji takiego obiektu:</p>
                        <pre className='line-numbers language-js'>
                            <code>
{`MySwingWorker worker = new MySwingWorker(); // Error:(6, 25) java: org.path.MySwingWorker is abstract; cannot be instantiated`}
                            </code>
                        </pre>

                        <p>Moesz utworzy konkretn implementacj:</p>
                        <pre className='line-numbers language-js'>
                            <code>
{`worker = new MySwingWorker<Void, Integer>() {
    @Override
    protected Void doInBackground() {
        TextArea.append("Performing checks...");
        CheckEntryAndStay();
        TextArea.append("Processing terminated");

        return null;
    }
`}
                            </code>
                        </pre>

                        <p className='snippets'>
                            Jest to "anonimowa" implementacja <span>MySwingWorker</span>, gdzie (jedyna, jak z tego
                            wynika) abstrakcyjna metoda <span>doInBackground</span> zostaa zaimplementowana.
                        </p>
                    </details>

                    <li>nie moe istnie klasa abstrakcyjna finalna <span>final</span></li>
                    <li>kada klasa jest abstrakcyjna jeli posiada chocia jedn metod abstrakcyjn, ktra:</li>

                    <ul className='nested-list'>
                        <li>posiada nazw</li>
                        <li>posiada typ zwracany</li>
                        <li>moe posiada przyjmowane argumenty</li>
                        <li>posiada modyfikator dostpu</li>
                        <li>nie posiada ciaa metody, czyli robi nic</li>
                    </ul>

                    <li>
                        jeeli klasy dziedzicz <span>extends</span> po klasie abstrakcyjnej, to klasa abstrakcyjna
                        wymusza na nich implementacj metod abstrakcyjnych
                    </li>

                    <li>mog posiada metody nieabstrakcyjne</li>
                    <li>mog posiada pola</li>
                    <li>mona dziedziczy tylko po jednej klasie</li>
                </ul>

                <hr />

                <h1>interface</h1>
                <ul className='snippet-list'>
                    <li>nie mog posiada pl (wyjtkiem jest pole <span>public static final</span>)</li>
                    <li>nie ma konstruktora i nie mona go stworzy</li>
                    <li>wszystkie metody w interfejsach s domylnie publiczne i abstrakcyjne i nie moe by innych</li>
                    <li>metody nie posiadaj ciaa (wyjtkiem jest metoda <span>default</span>)</li>
                    <li>interfejsy si implementuje, a nie dziedziczy</li>
                    <li>mona implementowa nieskoczenie wiele interfejsw</li>
                </ul>

                <hr />

                <h1>final</h1>
                <ul>
                    <li>jeeli pole jest finalne, to nie mona go zmieni</li>
                    <li>jeeli pole jest finalne, to to musi by zainicjalizowane</li>
                    <li>jeeli metoda jest finalna, to nie mona jej nadpisa</li>
                    <li>jeeli klasa jest finalna, to nie mona po niej dziedziczy</li>
                </ul>
            </article>
        </>
    );
};

