import React, {useEffect} from 'react';
import cover from '../../../img/cover/cover-javascript.webp';
import theNodeJsSystem from '../../../img/javascript/the-node.js-system.png';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const VarLet = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className='cover-wrapper'>
                <img src={cover} alt='html cover' className='cover'/>
            </div>
            <article className='article article-content'>
                <h1>Ptla zdarze</h1>
                <ul>
                    <li>Przykad operacji blokowania:</li>
                    <pre className='line-numbers language-js'>
                        <code>
{`let loop = (i, max) => {
    while (i < max) i++
        return i;
}

// This operation will block Node.js
// Because, it's CPU-bound
// You should be careful about this kind of code
loop(0, 1e+12)`}
                        </code>
                    </pre>

                    <li>Nieblokujcy przykad operacji we / wy:</li>
                    <pre className='line-numbers language-js'>
                        <code>
{`let i = 0

const step = max => {
    while (i < max) i++
        console.log('i = %d', i)
}

const tick = max => process.nextTick(step, max)

// this will postpone tick run step's while-loop to event loop cycles
// any other IO-bound operation (like filesystem reading) can take place
// in parallel
tick(1e+6)
tick(1e+7)

console.log('this will output before all of tick operations. i = %d', i)
console.log('because tick operations will be postponed')

tick(1e+8)`}
                        </code>
                    </pre>
                </ul>

                <div className='article-img-wrapper'>
                    <img src={theNodeJsSystem} alt='padding and margin'
                        className='article-img article-img-desktop'/>
                </div>

                <p>
                    Mwic prociej, Ptla zdarze jest jednowtkowym mechanizmem kolejki, ktry wykonuje kod zwizany z
                    procesorem do koca jego wykonania oraz kod zwizany z operacjami we / wy w sposb nieblokujcy.
                </p>

                <hr/>

                <h1>Uwagi dotyczce wydajnoci</h1>
                <p>
                    Operacje nieblokujce nie blokuj kolejki i nie wpywaj na wydajno ptli. Jednak operacje zwizane
                    z procesorem zablokuj kolejk, dlatego naley zachowa ostrono, aby nie wykonywa operacji
                    zwizanych z procesorem w kodzie Node.js. Node.js nieblokuje We / Wy, poniewa odcia prac do
                    jdra systemu operacyjnego, a gdy operacja we / wy dostarcza dane (jako zdarzenie), powiadomi Twj
                    kod o dostarczonych wywoaniach zwrotnych.
                </p>
            </article>
        </>
    );
};

export default VarLet;
